import React from 'react';
import ReactAudioPlayer from 'react-audio-player';
import { getRandomFolder } from '../exportedFileFolders';
import styles from './RewardItem.module.css';

function RewardItem({ isVictory }) {
    let sourceFolder = getRandomFolder(isVictory);

    let image = null;
    let audio = null;
    if (sourceFolder["wav"]) {
        audio = (
            <ReactAudioPlayer src={sourceFolder["wav"]} volume={0.5} autoPlay />
        );
    }
    if (sourceFolder["gif"]) {
        image = (
                    <img className={styles.imageFadeInAndOut} src={sourceFolder["gif"]} alt='Reward'/>
                )
    }

    return (
        <div className={styles.container}>
            {image}
            {audio}
        </div>
    );
}

export default React.memo(RewardItem);
