import React, { useEffect, useState } from 'react';
import { makeRequest } from '../utils';
import RewardItem from './RewardItem';

function App() {
    const [info, setInfo] = useState(null);
    const getInfo = () => {
        return new Promise((resolve, reject) => {
            makeRequest()
                .then((response) => {
                    console.info(response);
                    resolve(response);
                })
                .catch((err) => {
                    console.error(err);
                    reject(err);
                });
        });
    };
    useEffect(() => {
        getInfo()
            .then((response) => {
                setInfo(response);
            })
            .catch((err) => {
                // Do nothing.
            });
        let interval = setInterval(() => {
            getInfo()
                .then((response) => {
                    setInfo(response);
                })
                .catch((err) => {
                    // Do nothing.
                });
        }, 10000);
        return () => {
            clearInterval(interval);
        };
    }, []);
    if (!info) {
        return null;
    }
    // Temp
    let victory = false;
    let defeat = false;
    if (info.includes("We won")) {
        victory = true;
    }
    else if (info.includes("We lost")) {
        defeat = true;
        victory = false;
    }
    if (!defeat && !victory) {
        return null;
    }

    return (
        <RewardItem
            isVictory={victory}
        />
    );
}

export default React.memo(App);
