import axios from 'axios';

export function makeRequest() {
    return new Promise((resolve, reject) => {
        let passKey = process.env.REACT_APP_STREAMER_KEY;
        if (process.env.NODE_ENV !== "development") {
            const params = new URLSearchParams(window.location.search);
            passKey = params.get("pass");
            if (passKey !== "passKey") {
                reject("Wrong password");
            }
        }

        const options = {
            method: 'get',
            header: {
                'content-type': 'application/x-www-form-urlencoded'
            },
            params: {
                cn: 'chatAction',
                command: 'chatAction',
                action: 'battle',
                channel: 'chicken_lancer',
            },
            url: process.env.REACT_APP_GAME_API_URL
        };
        axios(options)
            .then((response) => {
                const errorMessage = response.data.errorMessage;
                if (errorMessage) {
                    console.error(errorMessage);
                    return reject(errorMessage);
                }
                resolve(response.data);
            })
            .catch((err) => {
                const errorText = getErrorString(err);
                console.error(errorText);
                reject(errorText)
            });
    });
}

function getErrorString(err) {
    if (typeof err === 'string') {
        return err;
    } else if (err.response) {
        return `Server error: ${err.response.status}`;
    } else if (err.request) {
        return `Server error: No response.`;
    } else if (err.message) {
        return `Application error: ${err.message}`;
    } else {
        console.error(err);
        return `Unknown error.`;
    }
}
